import React, { Component } from 'react';
import Checkbox from '../../../components/input-controls/Checkbox';
import TextInputField from '../../../components/input-controls/TextInputField';
import Textarea from '../../../components/input-controls/Textarea';

export default class ContactUsForm extends Component {
  render() {
    return (
      <div>
        <h2 id="question">        ? </h2>
        <Checkbox
          {...{
            id: this.props.id,
            type: 'checkbox',
            checkboxMessage: ''
          }}
        />
        <Checkbox
          {...{
            id: this.props.id,
            type: 'checkbox',
            checkboxMessage: ''
          }}
        />
        <Checkbox
          {...{
            id: this.props.id,
            type: 'checkbox',
            checkboxMessage: ''
          }}
        />
        <Checkbox
          {...{
            id: this.props.id,
            type: 'checkbox',
            checkboxMessage: ''
          }}
        />
        <TextInputField
          {...{
            handleChange: () => {},
            id: 'name',
            type: 'text',
            label: '...',
            pattern: '[A-Z,a-z--, ]*',
            errorMessage: '     '
          }}
        />
        <TextInputField
          {...{
            handleChange: () => {},
            id: 'email',
            type: 'email',
            label: '...',
            pattern:
              "^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$",
            errorMessage: '     '
          }}
        />
        <Textarea
          {...{
            value: '',
            id: 'note',
            handleChange: () => {},
            rows: 5,
            type: 'text',
            textareaMessage: ' :'
          }}
        />
        <p>
          <button
            className="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--accent"
            type="submit"
          >
            
            <span className="mdl-button__ripple-container">
              <span className="mdl-ripple" />
            </span>
          </button>
        </p>
      </div>
    );
  }
}



