import React from 'react';
import TeamMemberCard from './TeamMemberCard';

export default function TeamMembersList(props) {
  return (
    <div className="team-members-list">
      <h2 className="team-member-title">  </h2>
      <TeamMemberCard
        {...{
          stylingId: 'bojo',
          personName: ' ',
          personPosition: '  '
        }}
      />
      <TeamMemberCard
        {...{
          stylingId: 'didi',
          personName: ' ',
          personPosition: '  '
        }}
      />
    </div>
  );
}



